/*
 *----------------------------------------------------------
 *midi.audioDetect : 0.3.2 : 2015-03-26
 *----------------------------------------------------------
 *https://github.com/mudcube/midi.js
 *----------------------------------------------------------
 *Probably, Maybe, No... Absolutely!
 *Test to see what types of <audio> MIME types are playable by the browser.
 *----------------------------------------------------------
 */

const supports = {}; // object of supported file types
let pending = 0; // pending file types to process
const canPlayThrough = function (src) {
  // check whether format plays through
  pending++;
  const { body } = document;
  const audio = new window.Audio();
  const mime = src.split(';')[0];
  audio.id = 'audio';
  audio.setAttribute('preload', 'auto');
  audio.setAttribute('audiobuffer', true);
  audio.addEventListener(
    'error',
    function () {
      body.removeChild(audio);
      supports[mime] = false;
      pending--;
    },
    false
  );
  audio.addEventListener(
    'canplaythrough',
    function () {
      body.removeChild(audio);
      supports[mime] = true;
      pending--;
    },
    false
  );
  audio.src = `data:${src}`;
  body.appendChild(audio);
};

export const audioDetect = function (onsuccess) {
  // / detect jazz-midi plugin
  if (navigator.requestMIDIAccess) {
    const isNative = Function.prototype.toString
      .call(navigator.requestMIDIAccess)
      .indexOf('[native code]');
    if (isNative) {
      // has native midiapi support
      supports.webmidi = true;
    } else {
      // check for jazz plugin midiapi support
      for (let n = 0; navigator.plugins.length > n; n++) {
        const plugin = navigator.plugins[n];
        if (plugin.name.indexOf('Jazz-Plugin') >= 0) {
          supports.webmidi = true;
        }
      }
    }
  }

  // / check whether <audio> tag is supported
  if (typeof window.Audio === 'undefined') {
    return onsuccess({});
  }
  supports.audiotag = true;

  // / check for webaudio api support
  if (window.AudioContext || window.webkitAudioContext) {
    supports.webaudio = true;
  }

  // / check whether canPlayType is supported
  const audio = new window.Audio();
  if (typeof audio.canPlayType === 'undefined') {
    return onsuccess(supports);
  }

  // / see what we can learn from the browser
  let vorbis = audio.canPlayType('audio/ogg; codecs="vorbis"');
  vorbis = vorbis === 'probably' || vorbis === 'maybe';
  let mpeg = audio.canPlayType('audio/mpeg');
  mpeg = mpeg === 'probably' || mpeg === 'maybe';
  // maybe nothing is supported
  if (!vorbis && !mpeg) {
    onsuccess(supports);
    return;
  }

  // / or maybe something is supported
  if (vorbis)
    canPlayThrough(
      'audio/ogg;base64,'
    );
  if (mpeg)
    canPlayThrough(
      'audio/mpeg;base64,/+MYxAAAAANIAUAAAASEEB/jwOFM/0MM/90b/+RhST//w4NFwOjf///PZu////9lns5GFDv//l9GlUIEEIAAAgIg8Ir/JGq3/+MYxDsLIj5QMYcoAP0dv9HIjUcH//yYSg+CIbkGP//8w0bLVjUP///3Z0x5QCAv/yLjwtGKTEFNRTMuOTeqqqqqqqqqqqqq/+MYxEkNmdJkUYc4AKqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqq'
    );

  // / lets find out!
  const time = new Date().getTime();
  const interval = window.setInterval(function () {
    const now = new Date().getTime();
    const maxExecution = now - time > 5000;
    if (!pending || maxExecution) {
      window.clearInterval(interval);
      onsuccess(supports);
    }
  }, 1);
};
